import React, {useState, useEffect} from 'react';
import {Redirect, NavLink} from 'react-router-dom';
import cn from 'classnames';
import {Button, Checkbox, Form, Input, Switch, Select} from 'antd';
import {camera, pen} from '../../../common/images/account';
import add from '../../../common/images/auth/add.svg';
import photo from '../../../common/images/auth/photo.svg';
import {open} from '../../../common/images/partners';
import {api} from '../../../utils/api';
import Avatar from '../../../utils/avatar';
import {SelectItem} from '../../../utils/supports';

export const SecondStep = ({id, step, setStep, initials, name, width, years}) => {
  const [form] = Form.useForm();
  const [src, setSrc] = useState(null)
  const [selectLanguage, setSelectLanguage] = useState(null)
  const [switchForm, setSwitchForm] = useState(false)
  const [linkText, setLinkText] = useState('')
  const [inputText, setInputText] = useState('')
  const [links, setLinks] = useState([])
  const [redirectLogin, setRedirectLogin] = useState(false)
  const [language, setLanguage] = useState([])
  const [communication, setCommunication] = useState([])
  const [tags, setTags] = useState([])
  const [communicationError, setCommunicationError] = useState(false)
  const [tagsError, setTagsError] = useState(false)

  const dropLink = (link) => setLinks(links.filter(l => l !== link))

  const onTags = ({id, checked, name}) => {
    setTags([...tags.map(op => op.id === id ? {id, name, checked} : op)])
  }
  const onCommunication = ({id, checked, name}) => {
    setCommunication([...communication.map(op => op.id === id ? {id, name, checked} : op)])
  }

  const onFinish = (values) => {
    for (let i = 0; i < communication.length; i++) {
      if (communication[i].checked) {
        setCommunicationError(false)
        middlewareFinish(values)
        break
      }
      communication.length && setCommunicationError(true)
    }
    for (let i = 0; i < tags.length; i++) {
      if (tags[i].checked) {
        setTagsError(false)
        middlewareFinish(values)
        break
      }
      tags.length && setTagsError(true)
    }
  };

  const middlewareFinish = (values) => {
    !communicationError && !tagsError && onRequestFinish(values)
  }

  const onRequestFinish = async (values) => {
    let tagsFinish = [...tags.filter(op => op.checked).map(({id}) => id)]
    let communicationFinish = [...communication.filter(op => op.checked).map(({id}) => id)]
    const res =
      await api.profileUpdate({...values, selectLanguage, communication: communicationFinish, tags: tagsFinish, searchAccess: switchForm, id, links})
    setRedirectLogin(true)
  }

  const onInputText = e => {
    setLinkText(e.target.value)
    setInputText(e.target.value)
  }

  const onLinks = () => {
    linkText.length && setLinks([...new Set([...links, linkText])])
    setLinkText('')
    setInputText('')
  }


  useEffect(async () => {
    for (let i = 0; i < communication.length; i++) {
      if (communication[i].checked) {
        setCommunicationError(false)
        break
      }
      communication.length && setCommunicationError(true)
    }
    for (let i = 0; i < tags.length; i++) {
      if (tags[i].checked) {
        setTagsError(false)
        break
      }
      tags.length && setTagsError(true)
    }
  }, [communication, tags])

  useEffect(async () => {
    window.scrollTo(0, 0)
    const resTags = await api.tags()
    const resComm = await api.communication()
    const resLang = await api.language()
    setTags(resTags.data.map(t => ({...t, checked: false})))
    setCommunication(resComm.data.map(c => ({...c, checked: false})))
    setLanguage(resLang.data)
    setTagsError(false)
    setCommunicationError(false)
  }, []);

  if (redirectLogin) return <Redirect to='/login'/>

  return (
    <div className='second-step'>
      <div className='second-step__wrapper'>
        {/*<Avatar src={src} setSrc={setSrc} initials={initials} icon={photo}/>*/}
        <Avatar src={src} setSrc={setSrc} initials={initials} icon={src ? pen : camera} uid={id}/>

        <h3 className='second-step__name'>{name}</h3>
      </div>
      <Form
        form={form}
        name='register'
        onFinish={onFinish}
        scrollToFirstError>
        <div className='steps'>
          <div className={cn('step', {'current-step': step === 2})}/>
        </div>

        <Form.Item
          rules={[{required: true, message: 'Please select your language!'}]}
          name='language'
          label='Language'>
          <Select className='ant-select-customize-input' onChange={setSelectLanguage} placeholder='Please select a Language'>
            {language && language.map(l => <Select.Option key={l.name} value={l.name}>{l.name}</Select.Option>)}
          </Select>
        </Form.Item>

        <Form.Item name='university' label='University'
                   rules={[{required: true, message: 'Please input your University!'},{
                     validator: (_, value) => {
                       if (/^[A-Za-z0-9\s]+$/g.test(value)) {
                         return Promise.resolve()
                       }
                       return Promise.reject('Use only Latin letters!')
                     }
                   }]}>
          <Input placeholder='Enter school name'/>
        </Form.Item>

        <Form.Item name='faculty' label='Faculty' rules={[{required: true, message: 'Please input your Faculty!'},{
          validator: (_, value) => {
            if (/^[A-Za-z0-9\s]+$/g.test(value)) {
              return Promise.resolve()
            }
            return Promise.reject('Use only Latin letters!')
          }
        }]}>
          <Input placeholder='Enter faculty name'/>
        </Form.Item>

        <Form.Item name='year_of_graduation' label='Graduation year' rules={[{required: true, message: 'Please select your year!'}]}>
          <Select className='ant-select-customize-input' placeholder='Please select a year'>
            {years && years.map(year => <Select.Option key={year} value={year}>{year}</Select.Option>)}
          </Select>
        </Form.Item>

        <Form.Item label='Social media' rules={[{
          validator: (_, value) => {
            if (/^[A-Za-z0-9\s]+$/g.test(value)) {
              return Promise.resolve()
            }
            return Promise.reject('Use only Latin letters!')
          }
        }]}>
          <div className='flex-align-center' style={{position: 'relative'}}>
            <Input placeholder='Enter links' value={inputText} onChange={onInputText} style={{padding: '19px 30px 19px 11px'}}/>
            <img className='form__link-add' src={width < 681 ? open : add} alt='add new link' onClick={onLinks}
                 style={{position: 'absolute', right: width < 681 ? '12px' : '-80px', cursor: 'pointer'}}/>
          </div>
        </Form.Item>

        <div className='links'>
          {!!links.length && links.map((link, i) => <SelectItem key={link + i} option={link} dropSelect={dropLink}/>)}
        </div>

        <Form.Item className='border-none' label='Tags' validateStatus={false}
                   // help={!checkedTags && 'Select at least one checkbox'}
        >
          {tags && tags.map(op =>
            <div key={op.id}>
              <Checkbox checked={op.checked} value={op.name} onChange={() => onTags({id: op.id, checked: !op.checked, name: op.name})}>
                {op.name}
              </Checkbox>
            </div>
          )}
          <div className='checkbox-err'>{tags.length && tagsError && 'Select at least one checkbox'}</div>
        </Form.Item>

        <Form.Item name='bio' label='Any additional information' rules={[{
          validator: (_, value) => {
            if (/^[A-Za-z0-9.,?!)(\[\]}@#$%^&*\/\\";%{_\'\"\-+=\s]+$/g.test(value)) {
              return Promise.resolve()
            }
            return Promise.reject('Use only Latin letters!')
          }
        }]}>
          <Input.TextArea placeholder='Free format'/>
        </Form.Item>

        <div className='mb22'>
          <Form.Item className='border-none' validateStatus={false}
                     // help={!checkedCommunication && 'Select at least one checkbox'}
                     label='I prefer to contact by'>
            {communication && communication.map(op =>
              <div key={op.id}>
                <Checkbox checked={op.checked} value={op.name} onChange={() => onCommunication({id: op.id, checked: !op.checked, name: op.name})}>
                  {op.name}
                </Checkbox>
              </div>
            )}
            <div className='checkbox-err'>{communication.length && communicationError && 'Select at least one checkbox'}</div>
          </Form.Item>
        </div>
        <Form.Item className='border-none'>

          <div className='search-access'>
            <div className='search-access__title'>Available</div>
            <Switch onChange={setSwitchForm}/>
          </div>

          <div className='search-access__text'>
            By clicking here you open your
            page for other users and you can
            find buddies using our search tool.
          </div>
        </Form.Item>
        <Form.Item className='border-none'>
          <Button className='blue mt30' htmlType='submit' shape='round'>
            Save
          </Button>
          <NavLink to='/login'>
            <Button className='mt20' shape='round'>
              Finish later
            </Button>
          </NavLink>
        </Form.Item>
      </Form>

    </div>
  );
};
