import React, {useState, useEffect} from 'react';
import {NavLink, Redirect} from 'react-router-dom';
import {Button, Switch, Checkbox} from 'antd';
import {connect} from 'react-redux';
import {close} from '../../common/images/account';
import {down, up, add, arrow, report, open} from '../../common/images/partners';
import {setCurrentPage, CURRENT_PAGE} from '../../redux/reducers/page';
import {api} from '../../utils/api';
import Avatar from "../../utils/avatar";
import {PartnersIcon} from '../../utils/icon/partners';
import ModalCard from '../../utils/modal-card';
import {SelectItem} from '../../utils/supports';
import './index.scss';

const selects2 = [
  {
    placeholder: 'Tags',
    options: [
      {id: 1, checked: false, option: 'I\'m preparing for a case interview'},
      {id: 2, checked: false, option: 'I want to participate in a case competition'},
      {id: 3, checked: false, option: 'I want to develop my problem solving skills'},
      {id: 4, checked: false, option: 'I want to expand my professional network'}]
  },
  {
    placeholder: 'Country',
    options: [
      {id: 1, checked: false, option: 'USA'},
      {id: 2, checked: false, option: 'Russia'},
      {id: 3, checked: false, option: 'Spain'},
      {id: 4, checked: false, option: 'Israel'},
      {id: 5, checked: false, option: 'Armenia'},
      {id: 6, checked: false, option: 'Italy'}
    ]
  },
  {
    placeholder: 'Language',
    options: [
      {id: 1, checked: false, option: 'English'},
      {id: 2, checked: false, option: 'Arabic'},
      {id: 3, checked: false, option: 'Turkish'},
      {id: 4, checked: false, option: 'Azeri'},
      {id: 5, checked: false, option: 'French'},
      {id: 6, checked: false, option: 'Russian'},
      {id: 7, checked: false, option: 'Kazakh'},
      {id: 8, checked: false, option: 'Portuguese'},
      {id: 9, checked: false, option: 'Afrikaans'},
      {id: 10, checked: false, option: 'Swahili'},
      {id: 11, checked: false, option: 'Spanish'},
      {id: 12, checked: false, option: 'Dutch'},
      {id: 13, checked: false, option: 'Italian'},
      {id: 14, checked: false, option: 'German'}
    ]
  },
  {
    placeholder: 'My graduation year',
    checked: false
  },
  // {
  //   placeholder: 'Case Challenge',
  //   checked: false
  // }
]

const cells = [
  {title: 'I prefer to communicate by', key: 'communication_method'},
  {title: 'My graduation year', key: 'year_of_graduation'},
  {title: 'Any additional information', key: 'bio'},
  {title: 'Language', key: 'language'},
  {title: 'Country', key: 'country'},
  {title: 'Tags', key: 'search_target'},
  {title: 'Case Challenge', key: 'is_searchable'}
]

const Buddies = ({width, setCurrentPage, user, onMeAdded}) => {
  const [selects, setSelects] = useState(selects2)
  const [filters, setFilters] = useState({})
  const [selectFilter, setSelectFilter] = useState([])
  const [partners, setPartners] = useState(null)
  const [partner, setPartner] = useState(null)
  const [modal, setModal] = useState(false)
  const [isShow, setIsShow] = useState(false)
  const [isShowSearch, setIsShowSearch] = useState(false)
  const [bntMyContact, setBtnMyContact] = useState(false)
  const [case_challenge_participant, setCCP] = useState(false)
  const [my_graduation_year, setGY] = useState(false)
  const [redirectToRegister, setRedirectToRegister] = useState(false)
  const [toggleFilter, setToggleFilter] = useState(new Set)
  const [filterUrl, setFilterUrl] = useState('')
  const [i, setI] = useState(1)

  const onNextPartner = () => {
    setPartner(partners[i])
    setI(p => ++p)
    setBtnMyContact(false)
    onMeAdded()
  }

  const findPartner = async () => {
    const res = await api.partners(filterUrl.length ? `uid=${+localStorage.getItem('uid')}&${filterUrl}` : `uid=${+localStorage.getItem('uid')}`)
    Array.isArray(res.data) && setPartners(res.data)
    Array.isArray(res.data) && setPartner(res.data[0])
    setIsShowSearch(true)
    setIsShow(false)
    setBtnMyContact(false)
    onMeAdded()
  }

  const onChangeFilter = (p, {option, checked}) => {
    setFilters({...filters, [option]: checked})
    setSelectFilter(s => [...new Set([...s, option])])
    onFilterForSearchPartner(p)
    setBtnMyContact(false)
    onMeAdded()
  }

  useEffect(() => {
    setSelectFilter(s => s.filter(f => filters[f]))
    setIsShowSearch(false)
    setI(1)
  }, [filters])

  const dropSelectFilter = (option) => selectFilter.forEach(f => f === option && setFilters({...filters, [option]: false}))

  const clearFilter = async () => {
    setI(1)
    setPartners(null)
    setCCP(false)
    setGY(false)
    setBtnMyContact(false)
    setFilters({})
    setSelectFilter([])
    setToggleFilter(new Set())
    setFilterUrl('')
    onMeAdded()
  }

  const showClearAll = () => {
    if (case_challenge_participant || my_graduation_year) return true
    for (let i in filters) {
      if (filters[i]) return true
    }
    return false
  }

  const onFilterForSearchPartner = ({placeholder, id}) => {
    onMeAdded()
    const i = placeholder.toLowerCase() + '-' + id
    toggleFilter.has(i) ? toggleFilter.delete(i) : toggleFilter.add(i)
    setToggleFilter(new Set(toggleFilter))
  }

  const contactAdd = async (cId) => {
    onMeAdded()
    const res = await api.contactAdd({from_user: +user.id, to_user: cId})
    setBtnMyContact(true)
    console.log(res)
    res.data.result && setModal({message: res.data.message})
    !res.data.result && res.data && setModal('buddies added')
  }

  useEffect(() => {
    let tags = 'tags=';
    let country = 'country=';
    let language = 'language=';
    let ccp = 'case_challenge_participant=';
    let yg = 'year_of_graduation=';

    toggleFilter.size && Array.from(toggleFilter).forEach(i =>
      i.startsWith('tags')
        ? tags += i.substring(5) + ','
        : i.startsWith('country')
        ? country += i.substring(8) + ','
        : language += i.substring(9) + ',')

    case_challenge_participant ? ccp += 'true' + `${(my_graduation_year || tags.length > 5 || country.length > 8 || language.length > 9) ? '&' : ''}` : ccp = '';
    my_graduation_year ? yg += user.profileData.year_of_graduation + `${(tags.length > 5 || country.length > 8 || language.length > 9) ? '&' : ''}` : yg = '';
    tags.length > 5 ? tags = tags.slice(0, -1) + `${(country.length > 8 || language.length > 9) ? '&' : ''}` : tags = '';
    country.length > 8 ? country = country.slice(0, -1) + `${language.length > 9 ? '&' : ''}` : country = '';
    language.length > 9 ? language = language.slice(0, -1) : language = '';

    setFilterUrl(ccp + yg + tags + country + language)
  }, [toggleFilter, case_challenge_participant, my_graduation_year])

  useEffect(async () => {
    window.scrollTo(0, 0)
    setI(1)
    setCurrentPage(CURRENT_PAGE, 'Buddies')

    !localStorage.getItem('isAuth') && setRedirectToRegister(true)
    localStorage.getItem('isAuth') && !localStorage.getItem('is_second_step') && setModal('no register second step')

    const resTags = await api.tags()
    const resLang = await api.language()
    const resCountry = await api.country()
    setSelects(selects.map(s => s.placeholder === 'Tags'
      ? {...s, options: resTags.data.map(t => ({...t, option: t.name, checked: false}))}
      : s.placeholder === 'Language'
        ? {...s, options: resLang.data.map(t => ({...t, option: t.name, checked: false}))}
        : s.placeholder === 'Country'
          ? {...s, options: resCountry.data.map(t => ({...t, option: t.name, checked: false}))}
          : s))
    onMeAdded()
  }, [])

  if (redirectToRegister) return <Redirect to='/registration'/>

  return (
    <section className='partners'>
      <div className='partners__top'>
        {width > 1040 && <div className='partners__top-icon'>
          <PartnersIcon/>
        </div>}
        <div className='partners__top-headers'>
          <h1 className='partners__top-headers__title'>
            Buddies
            {width <= 1040 && <div className='partners__top-icon'>
              <PartnersIcon/>
            </div>}
          </h1>
          <h2 className='partners__top-headers__subtitle'>Find a buddy to learn and practice cases together.</h2>
        </div>
      </div>

      <div className='partners__main'>
        <div className='partners__main-wrapper'>
          <div className='partners__main-wrapper__filter'>
            <div className='partners__main-wrapper__top'>
              <div className='partners__main-wrapper__top-title' onClick={() => setIsShow(!isShow)}>
                <span>Filter</span>
                {width <= 1040 && (isShow
                  ? <img src={close} alt='open filters'/>
                  : <img src={open} alt='open filters'/>)}
              </div>
              {width > 1040 && showClearAll() &&
              <div className='partners__main-wrapper__top-clear' onClick={clearFilter}>
                Clear all
              </div>}
            </div>

            {width > 1040 && selects.map(({placeholder, options}) =>
              <Filter key={placeholder} placeholder={placeholder}
                      checkList={filters}
                      case_challenge_participant={case_challenge_participant}
                      setCCP={setCCP}
                      my_graduation_year={my_graduation_year}
                      setGY={setGY}
                      options={options} onChange={onChangeFilter}/>)}

            {isShow && selects.map(({placeholder, options}) =>
              <Filter key={placeholder} placeholder={placeholder}
                      checkList={filters}
                      case_challenge_participant={case_challenge_participant}
                      setCCP={setCCP}
                      my_graduation_year={my_graduation_year}
                      setGY={setGY}
                      options={options} onChange={onChangeFilter}/>)}

            {width <= 1040 && isShow &&
            <div className='partners__main-wrapper__top-btn'>
              <Button onClick={clearFilter}>Clear all</Button>
              <Button className='blue' onClick={() => findPartner()}>
                Apply
              </Button>
            </div>
            }
          </div>
          <div className='flex-center'>
            <div className='partners__main-wrapper__left'>
              <div className='partners__main-wrapper__left-filters'>
                {!!selectFilter.length && selectFilter.map(option => <SelectItem key={option} option={option} dropSelect={dropSelectFilter}/>)}
              </div>
              <div className='partners__main-wrapper__left-score contacts__score' style={{marginLeft: '20px'}}>Buddies: <span>{partners ? partners.length : 0}</span></div>
              <div className='partners__main-wrapper__left-card'>
                <Card canUserSearchPartner={user.profileData&&user.profileData.is_searchable} isShowSearch={isShowSearch}
                      partner={partner} findPartner={findPartner} setModal={setModal}
                      onNextPartner={onNextPartner} secondStep={localStorage.getItem('is_second_step')} bntMyContact={bntMyContact} contactAdd={contactAdd}/>
              </div>
            </div>
          </div>
        </div>
      </div>
      {modal && <ModalCard reason={modal.message ? 'error' : modal} message={modal.message && modal.message} setModal={setModal}/>}
    </section>
  );
};

export default connect(({windowSize, user}) => ({width: windowSize.width, user}), {setCurrentPage})(Buddies);

const Filter = ({placeholder, options = [], onChange, case_challenge_participant, my_graduation_year, setCCP, setGY, checkList}) => {
  const ccp = 'Case Challenge';
  const gy = 'My graduation year';
  const [show, setShow] = useState(false);

  const onCheckboxChange = (id, {checked, option}) => {
    onChange({placeholder, id}, {option, checked: !checked})
  }

  return (
    <div className='filter'>
      <div className='filter__placeholder' onClick={() => setShow(!show)}>
        {placeholder}
        {!(placeholder === ccp || placeholder === gy) &&
        <div className='filter__arrow'>
          <img className={show ? 'arrow-down' : 'arrow-up'} src={show ? up : down} alt='arrow'/>
        </div>}

        {placeholder === ccp && <Switch checked={case_challenge_participant} onChange={setCCP}/>}
        {placeholder === gy && <Switch checked={my_graduation_year} onChange={setGY}/>}

      </div>
      {show && !(placeholder === ccp || placeholder === gy) &&
      <div className='filter__show'>
        {!!options.length && options.map(({id, checked, option}) =>
          <Checkbox key={option} checked={!!Object.keys(checkList).length ? checkList[option] : checked}
                    onChange={() => onCheckboxChange(id, {checked: !!Object.keys(checkList).length ? checkList[option] : checked, option})}>
            {option}
          </Checkbox>
        )}
      </div>
      }
    </div>
  )
};

const Card = ({partner, contactAdd, bntMyContact, canUserSearchPartner, findPartner, setModal, onNextPartner, isShowSearch}) => {

  return (
    <div className='card'>
      {
        !canUserSearchPartner
          ? <CardCanSearchPartner/>
          : !isShowSearch
          ? <CardSearchPartner secondStep={localStorage.getItem('is_second_step')} setModal={setModal} findPartner={findPartner}/>
          : partner
            ? <CardPartner partner={partner} contactAdd={contactAdd} bntMyContact={bntMyContact} setModal={setModal} onNextPartner={onNextPartner}/>
            : <CardNotFoundPartner/>
      }
    </div>
  )
};

const CardSearchPartner = ({secondStep, setModal, findPartner}) =>
  <div className='card-search-partner padding-card'>
    <p>By using search filters you can
      organize the list according to your
      needs.</p>
    <Button className='blue' shape='round' size='large'
            onClick={() => secondStep ? findPartner() : setModal('buddies no second step')}>Find a buddy</Button>
  </div>

const CardPartner = ({partner, contactAdd, onNextPartner, bntMyContact, setModal}) => {
  const {id, user, lastActive, photo} = partner
  return (
    <div className='card-partner padding-cart-partner'>
      <div className='card-partner__avatar'>
        {photo !== 'http://case-university.com:8080/media/default.png'
          ? <Avatar src={'http://case-university.com/' + photo.replace(/^[a-zA-Z]{3,5}\:\/{2}[a-zA-Z0-9_.:-]+\//, '')} alt='avatar'/>
          : <div className='card-partner__avatar-img'>
            {`${user.first_name[0]}${user.last_name[0]}`}
          </div>
        }

        <div className='report' onClick={() => setModal('report')}>
          Report
          <img src={report} alt='report'/>
        </div>
      </div>
      <div className='card-partner__info'>
        <div className='card-partner__info-name'>
          {`${user.first_name} ${user.last_name}`}
          <div className='last-active'>
            Last seen:<span>{user.last_login}</span>
          </div>
        </div>
        <div className='card-partner__info-cells'>
          {cells && cells.map(({title, key}) => <CardPartnerCell key={key} title={title} text={partner[key]}/>)}
        </div>
        <div className='card-partner__info-btn'>
          <Button className='blue' disabled={bntMyContact} onClick={() => contactAdd(user.id)}>
            {bntMyContact ? 'Already added' : <>Add Buddy <img src={add} alt='add'/></>}

          </Button>
          <Button className='card-partner__info-btn__last' onClick={() => onNextPartner()}>
            Next
            <img src={arrow} alt='arrow'/>
          </Button>
        </div>
      </div>
    </div>
  )
}

export const CardPartnerCell = ({title, text}) => {
  if (Array.isArray(text) && text.length === 0) text = null
  return (
    <>
      {text !== null && title !== 'Case Challenge' &&
        <div className='card-partner-cell'>
          <h5 className='card-partner-cell__title'>
            {title}
          </h5>
          <p className='card-partner-cell__text'>
            {title === 'My graduation year' && text && <span>{text}</span>}
            {title === 'Any additional information' && text && <>{text}</>}
            {title === 'Country' && text && <span>{text.name}</span>}
            {title === 'Case Challenge' && <span className={text ? 'participant' : undefined}>{text ? 'Yes' : 'No'}</span>}
            {title === 'Tags' && text && text.map(({name}) => <div key={name} className='tags'>{name}</div>)}
            {title === 'Language' && text && text.map(({name}) => <div key={name}>{name}</div>)}
            {title === 'I prefer to communicate by' && text && text.map(({name}) => <div key={name}>{name}</div>)}
          </p>
        </div>
      }</>
  )
}

const CardCanSearchPartner = () =>
  <div className='card-can-search-partner padding-card'>
    <h3>Search not available</h3>
    <p>To start searching for a buddy you need to open your profile as well. Please make changes in your profile settings</p>
    <Button className='blue' shape='round' size='large'><NavLink to='/account/profile'>Go to profile</NavLink></Button>
  </div>

const CardNotFoundPartner = () =>
  <p className='card-not-found-partner padding-card'>
    Seems like there is no one that
    matches your criteria. Please
    change filters and try again!
  </p>
