import React, {useState, useEffect} from 'react';
import {NavLink} from 'react-router-dom';
import {Button} from 'antd';
import cn from 'classnames';
import {close} from '../../../common/images/materials';
import {report} from '../../../common/images/partners';
import {ashbin, add} from '../../../common/images/account';
import {api} from '../../../utils/api';
import Avatar from "../../../utils/avatar";
import ModalCard from '../../../utils/modal-card';
import './index.scss';

const Contacts = ({id, onPage, width, onMeAdded}) => {
  const [modal, setModal] = useState(null)
  const [partners, setPartners] = useState(null)
  const [partnerId, setPartnerId] = useState(null)
  const [isLoading, setIsLoading] = useState(true)
  const [isContactIAdded, setIsContactIAdded] = useState(null)

  const reAddUserContacts = async () => {
    const resPartners = await api.userContacts(+id)

    setPartners([...resPartners.data.map(i => i.approved ? {...i, status: 'my contact'} : +i.from_user.id === +id ? {...i, status: 'added'} : {...i, status: 'I was added'})])
    onMeAdded()
  }

  const contactDelete = async (cId) => {
    const res = await api.contactDelete({from_user: +cId, to_user: +id})
    reAddUserContacts()
    onMeAdded()
  }

  const contactDelete2 = async (cId) => {
    const res = await api.contactDelete({from_user: +id, to_user: +cId})
    reAddUserContacts()
    onMeAdded()
  }

  const contactApprove = async (cId) => {
    const res = await api.contactApprove(isContactIAdded ? {from_user: +id, to_user: +cId} : {from_user: +cId, to_user: +id})
    reAddUserContacts()
    onMeAdded()
  }

  const onModal = (modal, id) => {
    setModal(modal)
    setPartnerId(id)
  }

  useEffect(async () => {
    onPage('contacts')
    const res = await api.userContacts(+id)
    res.data &&
    setPartners([...res.data.map(i => i.approved ? {...i, status: 'my contact'} : +i.from_user.id === +id ? {...i, status: 'added'} : {...i, status: 'I was added'})])
    onMeAdded()
    setIsLoading(false)
  }, [])

  if (isLoading) return <div>Loading</div>

  //contacts: [{id: 2, first_name: "test name", last_name: "test surname", email: "denzzeuro@gmail.com"}]
  return (
    <div className='contacts'>
      {Array.isArray(partners) && partners.length > 0
        ? <>
          {/*<div className='contacts__score'>Contacts: <span>{partners.length}/50</span></div>*/}
          {partners.map((partner, i) =>
            <div key={partner.to_user.first_name + i} className='card'>
              <CardPartner partner={+partner.from_user.id === +id ? partner.to_user : partner.from_user} partners={partners} status={partner.status}
                           setPartners={setPartners} setModal={onModal} setIsContactIAdded={setIsContactIAdded} uid={+id}
                           contactApprove={contactApprove} contactDelete={contactDelete} contactDelete2={contactDelete2}
                           width={width}/>
            </div>)}
        </>
        :
        <div className='contacts-not'>
          <div className='contacts-not__text'>You don't have any contacts yet.</div>
          <NavLink to='/buddies'><Button className='blue'>Find a buddy</Button></NavLink>
        </div>
      }
      {modal && <ModalCard reason={modal}
                           partners={partners} partnerId={partnerId} setPartners={setPartners}
                           setModal={setModal} contactDelete={contactDelete} contactDelete2={contactDelete2}/>}
    </div>
  );
};

export default Contacts;

const handlers = {
  'my contact': {handler: 'delete', btn: ashbin},
  'added': {handler: 'no add', btn: 'Recall'},
  'I was added': {handler: 'cancel', btn: close, btn2: add}
}

const cells = [
  {title: 'I prefer to communicate by', key: 'communication_method'},
  {title: 'My graduation year', key: 'year_of_graduation'},
  {title: 'Any additional information', key: 'bio'},
  {title: 'Language', key: 'language'},
  {title: 'Country', key: 'country'},
  {title: 'Tags', key: 'search_target'},
  {title: 'Case Challenge participant', key: 'is_searchable'}
]

//contacts: [{id: 2, first_name: "test name", last_name: "test surname", email: "denzzeuro@gmail.com"}]
const CardPartner = ({partner = {}, status, partners, setPartners, setModal, contactApprove, contactDelete, contactDelete2, width, uid, setIsContactIAdded}) => {
  const [isShow, setIsShow] = useState(false)
  useEffect(() => {
    setIsContactIAdded(+partner.id === +uid ? true : false)
  }, [partner])
  return (
    <div className='card-partner'>
      <div className='card-partner__avatar'>
        {/*<img src={partner.photo} alt='avatar'/>*/}
        <Avatar src={partner.profile.photo !== 'http://case-university.com:8080/media/default.png' && partner.profile.photo} initials={partner.first_name[0]+partner.last_name[0]}/>
        {isShow && <div className='report' onClick={() => setModal('report')}>
          Report <img src={report} alt='report'/>
        </div>}
        {width <= 778 && !isShow &&
        <Buttons id={partner.id} partners={partners} contactApprove={contactApprove} contactDelete={contactDelete} contactDelete2={contactDelete2} status={status} setModal={setModal} setPartners={setPartners}/>}
      </div>

      <div className='card-partner__info'>
        <div className='card-partner__info-wrapper'>
          <div className='card-partner__info-name' onClick={() => setIsShow(!isShow)}>
            <span className='card-partner__info-name-first'>{partner.first_name}</span>
            <span className='card-partner__info-name-last'>
               {partner.last_name}&nbsp;{isShow ? <div className='arrow-up'/> : <div className='arrow-down'/>}
             </span>
          </div>

          {status in handlers && handlers[status].handler === 'no add'
            ? <div className='added-text'>Request has been sent</div>
            : <div className='last-active'>
              Last seen:<span>{partner.last_login}</span>
            </div>
          }

          {isShow && status === 'my contact' && <div className='email-phone' style={{margin: '0 0 20px 0'}}>
            <div className='email-phone__cell' style={{display: 'flex'}}>
              <div className='email-phone__title card-partner-cell__title'>Email:</div>
              <span className='card-partner-cell__text card-partner-cell__text' style={{margin: '0 0 0 10px'}}>
                <a href={`mailto:${partner.email}`}>{partner.email}</a>
              </span>
            </div>
            <div className='email-phone__cell' style={{display: 'flex'}}>
              <div className='email-phone__title card-partner-cell__title'>Mobile:</div>
              <span className='card-partner-cell__text card-partner-cell__text' style={{margin: '0 0 0 10px'}}>
                <a href={`tel:${+partner.profile.prefix.toString() + partner.profile.phone.toString()}`}>
                  +{partner.profile.prefix.toString() + partner.profile.phone.toString()}
                </a>
              </span>
            </div>
          </div>}

          {isShow && <div className='card-partner__info-cells'>
            {/*{cells && cells.map(({title, text, emailPhone}, i) => <CardPartnerCell key={title + i} emailPhone={emailPhone} title={title} text={text}/>)}*/}
            {cells && cells.map(({title, key}) => <CardPartnerCell key={key} title={title} text={partner.profile[key]}/>)}
          </div>}
        </div>

        {width < 778 && isShow &&
        <Buttons id={partner.id} partners={partners}
                 contactApprove={contactApprove} contactDelete={contactDelete} contactDelete2={contactDelete2}
                 status={status} setModal={setModal} setPartners={setPartners} isShow={isShow}
                 width={width}/>}

        {width >= 778 &&
        <Buttons id={partner.id} partners={partners}
                 contactApprove={contactApprove} contactDelete={contactDelete} contactDelete2={contactDelete2}
                 status={status} setModal={setModal} setPartners={setPartners}/>}
      </div>
    </div>
  )
}

const Buttons = ({id, status, partners, setPartners, setModal, isShow, width, contactApprove, contactDelete, contactDelete2}) => {
  const onClick = () => {
    setModal(handlers[status].handler, id, contactDelete2, contactDelete)
  }

  return <div className='card-partner__info-btn'>

    {status in handlers && handlers[status].handler === 'no add'

      ? <Button className={cn({'btn-no-add': isShow})} onClick={onClick}>{handlers[status].btn}</Button>

      : <div className='btn-status'>
        <ButtonStatus btn={handlers[status].btn} btn2={handlers[status].btn2}
                      status={handlers[status].handler} width={width} isShow={isShow}
                      partnerId={id} partners={partners}
                      contactApprove={contactApprove} contactDelete={contactDelete}
                      setPartners={setPartners} setModal={setModal}/>
        {handlers[status].handler === 'cancel' && <div className='btn-status__text'>Add to reply</div>}

      </div>
    }
  </div>
}

const ButtonStatus = ({partnerId, btn, btn2, status, setModal, isShow, width, contactApprove, contactDelete}) => {
  return <>
    {btn2 && <Button className={status === 'cancel' ? 'btn-cancel' : 'circle'}
                     onClick={() => contactApprove(partnerId)}>
      <img src={btn2} alt='icon'/>
    </Button>}
    <Button className={status === 'cancel' ? 'btn-cancel btn-cancel-padding' : 'circle'}
            onClick={() => setModal(status, partnerId, contactApprove, contactDelete)}>
      {width < 478 && isShow && status === 'delete' ?
        <span className='ashbin-text'> &nbsp;<img src={btn} alt='icon'/></span> : <img src={btn} alt='icon'/>}
    </Button>
  </>
}

export const CardPartnerCell = ({title, text}) => {
  if (Array.isArray(text) && text.length === 0) text = null
  return (
    <>
      {text !== null && title !== 'Case Challenge participant' &&
      <div className='card-partner-cell'>
        <h5 className='card-partner-cell__title'>
          {title}
        </h5>
        <p className='card-partner-cell__text'>
          {title === 'My graduation year' && text && <span>{text}</span>}
          {title === 'Any additional information' && text && <span>{text}</span>}
          {title === 'Country' && text && <span>{text.name}</span>}
          {title === 'Case Challenge' && <span className={text ? 'participant' : undefined}>{text ? 'Yes' : 'No'}</span>}
          {title === 'Tags' && text && text.map(({name}) => <div key={name} className='tags'>{name}</div>)}
          {title === 'Language' && text && text.map(({name}) => <div key={name}>{name}</div>)}
          {title === 'I prefer to communicate by' && text && text.map(({name}) => <div key={name}>{name}</div>)}
        </p>
      </div>
      }</>
  )
}
