import {api} from '../../../utils/api';

const initState = {}

const SET_CARDS = 'SET_CARDS'

export const materials = (state = initState, action) => {
  switch (action.type) {
    case SET_CARDS : {
      return {...state, cards: action.cards}
    }
    default: {
      return state
    }
  }
}

const setCards = (cards) => ({type: SET_CARDS, cards})

export const getCards = (filter= '') => async (dispatch) => {
  const res = await api.getMaterialCards(filter)
  dispatch(setCards(res.data))
};

export const initMaterials = ({filter})  => async (dispatch) => {
  dispatch(getCards(filter))
}
