import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import cn from 'classnames';
import {Switch, Route, NavLink} from 'react-router-dom';
import {setMeAdded} from "../../redux/reducers/user";
import {api} from "../../utils/api";
import Avatar from '../../utils/avatar';
import {SearchAccess} from '../../utils/supports';
import Contacts from './Contacts';
import MyCases from './MyCases';
import Profile from './Profile';
import Settings from './Settings';
import {setCurrentPage, CURRENT_PAGE} from '../../redux/reducers/page';
import {pen, camera} from '../../common/images/account';
import './index.scss';

const PrivateAccount = ({page, setCurrentPage, id, photo, first_name, last_name, setMeAdded, onMeAdded, profileData}) => {
  const [currentShowPage, setCurrentShowPage] = useState('contacts')
  const [src, setSrc] = useState(localStorage.getItem('photo'))
  const [searchAccess, setSearchAccess] = useState(profileData&&profileData.is_searchable)
  const [width, setWidth] = useState(document.documentElement.clientWidth)
  const [communication, setCommunication] = useState([])
  const [tags, setTags] = useState([])
  const [isLoading, setIsLoading] = useState(true)

  const onResize = e => setWidth(e.target.innerWidth)
  useEffect(async () => {
    window.scrollTo(0, 0)
    setCurrentPage(CURRENT_PAGE, 'PrivateAccount')
    const resTags = await api.tags()
    const resComm = await api.communication()
    setTags(resTags.data.map(t => ({...t, checked: false})))
    setCommunication(resComm.data.map(c => ({...c, checked: false})))
    setIsLoading(false)
    setWidth(document.documentElement.clientWidth)
    window.addEventListener('resize', onResize)
    return () => {
      window.removeEventListener('resize', onResize)
    }
  }, [])

  if (isLoading) return <div>Loading</div>

  return (
    <div className='account'>
      <div className='account__top'>
        <div className='flex-align-center'>
          <div className='account__top-avatar'>
            <Avatar src={src} setSrc={setSrc} initials={first_name && first_name[0] + last_name[0]} icon={src ? pen : camera}/>
          </div>
          <div className='account__top-name'>
            {first_name + ' ' + last_name}
          </div>
        </div>
        {width > 778 && currentShowPage === 'profile' && <div className='account__top-switch'>
          <SearchAccess searchAccess={searchAccess} setSearchAccess={setSearchAccess}/>
        </div>}
      </div>
      <div className='account__main' style={currentShowPage === 'profile' ? {marginTop: '60px'} : {marginTop: '159px'}}>
        <div className='account__main-menu'>
          <ul className='account__main-menu__lists'>
            <li className={cn('account__main-menu__lists-list', {'color-black': 'contacts' === currentShowPage})}><NavLink to='/account/contacts'>My&nbsp;Buddies</NavLink></li>
            <li className={cn('account__main-menu__lists-list', {'color-black': 'profile' === currentShowPage})}><NavLink to='/account/profile'>Profile</NavLink></li>
            <li className={cn('account__main-menu__lists-list', {'color-black': 'my-cases' === currentShowPage})}><NavLink to='/account/my-cases'>My&nbsp;cases</NavLink></li>
            <li className={cn('account__main-menu__lists-list', {'color-black': 'settings' === currentShowPage})}><NavLink to='/account/settings'>Settings</NavLink></li>
          </ul>
          {width <= 778 && currentShowPage === 'profile' &&
          <div className='account__top-switch'><SearchAccess searchAccess={searchAccess} setSearchAccess={setSearchAccess}/></div>}
        </div>

        <div className='account__main-wrapper'>
          <Switch>
            <Route path='/account/contacts' exact render={() => <Contacts onMeAdded={onMeAdded} id={id} width={width} setMeAdded={setMeAdded} onPage={setCurrentShowPage}/>}/>
            <Route path='/account/profile' exact render={() => <Profile defCommunication={communication} defTags={tags} onPage={setCurrentShowPage} searchAccess={searchAccess}/>}/>
            <Route path='/account/my-cases' exact render={() => <MyCases onPage={setCurrentShowPage}/>}/>
            <Route path='/account/settings' exact render={() => <Settings id={id} onPage={setCurrentShowPage}/>}/>
          </Switch>
        </div>
      </div>
    </div>
  );
};

export default connect(({page, user: {id, first_name, last_name, profileData, photo}}) => ({...page, id, profileData, photo, first_name, last_name}), {setCurrentPage, setMeAdded})(PrivateAccount);
