import React, {useEffect, useRef, useState} from 'react';
import {Input, Button, Form} from 'antd';
import {api} from '../../../utils/api';
import {NoVisible, Visible} from '../../../utils/icon/eye';
import Layout from '../../../utils/layout/service';
import ModalCard from '../../../utils/modal-card';

const PasswordRecovery = ({isMenuShow, setIsMenuShow}) => {
  const [form] = Form.useForm();
  const [isVisible, setIsVisible] = useState(false);
  const [isVisible2, setIsVisible2] = useState(false);
  const [loading, setLoading] = useState(false);
  const [confirmation_token, setConfirmationToken] = useState(null);
  const [uid, setUid] = useState(null);
  const [modal, setModal] = useState(null)
  const passwordRef = useRef(null)
  const passwordRef2 = useRef(null)

  const updatePassword = async (value) => {
    setLoading(true)
    const res = await api.updatePassword({...value, confirmation_token, uid})
    res.data.result === 'error' && setModal(res.data.messenger.non_field_errors[0])
    res.data.result === 'ok' && setModal({reason: 'password changed'})
    setLoading(false)
  }

  useEffect(() => {
    isVisible && passwordRef
      ? passwordRef.current.input.type = 'text'
      : passwordRef.current.input.type = 'password'
    isVisible2 && passwordRef2
      ? passwordRef2.current.input.type = 'text'
      : passwordRef2.current.input.type = 'password'
  }, [isVisible, isVisible2])

  useEffect(async () => {
    window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, (_, key, value) => {
      key === 'uid' && setUid(value)
      key === 'confirmation_token' && setConfirmationToken(value)
    });
  }, [])

  return (
    <Layout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}>
      <Form name='password-recovery'
            form={form}
            className='login-form'
            onFinish={updatePassword}>

        <div className='forgot'>
          <h1 className='title'>Change your password</h1>
          <Form.Item
            name='new_password1'
            label='New password'
            rules={[
              {required: true, message: 'Please input your password!'},
              {
                validator: (_, value) => {
                  if (/(?=.*[0-9])(?=.*[!@#$%+^&{}_*])(?=.*[a-z])(?=.*[A-Z])[0-9!@#$+%{}_^&*a-zA-Z]{6,}/g.test(value)) {
                    return Promise.resolve()
                  }
                  return Promise.reject('The password must contain at least 8 characters (Latin letters, numbers and at least one character: ! * + % @ ^ & { } _ #)')
                }
              }
            ]}>
            <Input.Password ref={passwordRef}
                            iconRender={_ => (isVisible
                              ? <Visible isVisible={isVisible} setIsVisible={setIsVisible}/>
                              : <NoVisible isVisible={isVisible} setIsVisible={setIsVisible}/>)}/>
          </Form.Item>

          <Form.Item
            name='new_password2'
            label='Re-enter new password'
            dependencies={['password']}
            rules={[
              {
                required: true,
                message: 'Please confirm your password!',
              },
              ({getFieldValue}) => ({
                validator(rule, value) {
                  if (!value || getFieldValue('new_password1') === value) {
                    return Promise.resolve();
                  }
                  return Promise.reject('Password doesnt match');
                },
              }),
            ]}>
            <Input.Password ref={passwordRef2}
                            iconRender={_ => (isVisible2
                              ? <Visible isVisible={isVisible2} setIsVisible={setIsVisible2}/>
                              : <NoVisible isVisible={isVisible2} setIsVisible={setIsVisible2}/>)}
            />
          </Form.Item>
          <Form.Item className='border-none'>
            <Button className='blue' htmlType='submit' loading={loading}>Save</Button>
          </Form.Item>
        </div>
      </Form>
      {modal && <ModalCard reason={modal.reason ? modal.reason : 'auth'} message={!modal.reason && modal} setModal={setModal}/>}
    </Layout>
  );
};

export default PasswordRecovery;
