export default connect(({materials}) => ({materials}), {setCurrentPage, getCards})(Materials);

const Faq = ({title, text}) => {
  const [isHide, setIsHide] = useState(true)

  return (
    <div className='faq'>
      <div className='faq-top' onClick={() => setIsHide(!isHide)}>
        <h3 className='faq-top__title'>{title}</h3>
        <div className='faq-top__arrow'>
          <img className={isHide ? 'arrow-down' : 'arrow-up'} src={isHide ? arrowDown : arrowUp} alt='arrow'/>
        </div>
      </div>
      {!isHide &&
      <div className='faq-text'>
        <p>{text}</p>
      </div>
      }
    </div>
  )
}


const CardLibrary = ({img, industry, operations, title, description, fullText, document}) => {
  const [showFullText, setShowFullText] = useState(false)
  return (
    <Card
      className='card-library'
      cover={
        <div className='card-library__img'>
          <h5 className='card-library__img-title'>{industry.name}</h5>
          <img src={img} alt='case library'/>
        </div>
      }
      actions={[<Download link={document}/>]}>
      <div className='card-library__info'>
        <h4 className='card-library__info-category'>{operations.name}</h4>
        <h3 className='card-library__info-title'>{title}</h3>
        {showFullText
          ? <p className='card-library__info-text'>{fullText} <span onClick={() => setShowFullText(false)}> (Hide full)</span></p>
          : <p className='card-library__info-text'>{description} {fullText && <span onClick={() => setShowFullText(true)}> (Read full)</span>}</p>}
      </div>
    </Card>
  )
}
