const initState = {
  isAuth: true,
  id: 1,
  'My graduation year': '2222 ',
  Country: 'USA',
  first_name: null,
  last_name: null,
  is_searchable: false,
  Language: ['English', 'Russian'],
  Tags: ['Preparing for an interview', 'I am looking for a team to participate'],
  'Case Challenge participant': 'Yes',
  canUserSearchPartner: true,
  contacts: [],
  myCases: [],
  authentication_token: null,
  meAdded: 0
}

export const USER_IS_AUTH = 'USER_IS_AUTH';
export const SET_USER_ID = 'SET_USER_ID';
export const SET_USER_TOKEN = 'SET_USER_TOKEN';
export const USER_IS_SECOND_STEP = 'USER_IS_SECOND_STEP';
export const SET_USER_NAME = 'SET_USER_NAME';
export const USER_PROFILE_DATA = 'USER_PROFILE_DATA';
export const SET_USER_AVATAR = 'SET_USER_AVATAR';
export const USER_ME_ADDED = 'USER_ME_ADDED';

export const user = (state = initState, action) => {
  switch (action.type) {
    case USER_IS_AUTH: {
      return {...state, isAuth: action.isAuth}
    }

    case SET_USER_ID: {
      !localStorage.getItem('uid') && localStorage.setItem('uid', action.id)
      return {...state, id: action.id}
    }

    case SET_USER_NAME: {
      !localStorage.getItem('name') && localStorage.setItem('name', JSON.stringify({first_name: action.first_name, last_name: action.last_name}))
      return {...state,
        first_name: action.first_name,
        last_name: action.last_name
      }
    }

    case SET_USER_TOKEN: {
      !localStorage.getItem('token') && localStorage.setItem('token', action.token)
      return {...state, token: action.token}
    }

    case SET_USER_AVATAR: {
      !localStorage.getItem('photo') && localStorage.setItem('photo', action.photo)
      return {...state, photo: action.photo}
    }

    case USER_PROFILE_DATA: {
      localStorage.setItem('profileData', JSON.stringify(action.profileData))
      return {...state, profileData: {...action.profileData}}
    }

    case USER_IS_SECOND_STEP: {
      return {...state, is_second_step: action.is_second_step}
    }

    case USER_ME_ADDED: {
      return {...state, meAdded: action.meAdded}
    }

    default: {
      return state
    }
  }
}

export const setUserIsAuth = (type, isAuth) => ({type, isAuth})
export const setUserId = (type, id) => ({type, id})
export const setUserName = (type, first_name, last_name) => ({type, first_name, last_name})
export const setUserToken = (type, token) => ({type, token})
export const setUserAvatar = (type, photo) => ({type, photo})
export const setUserIsSecondStep = (type, is_second_step) => ({type, is_second_step})
export const setUserProfileData = (type, profileData) => ({type, profileData})
export const setMeAdded = (type, meAdded) => ({type, meAdded})
