import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import cn from 'classnames';
import {setCurrentPage} from '../../../redux/reducers/page';
import Layout from '../../../utils/layout/service';
import ModalCard from "../../../utils/modal-card";
import {FirstStep} from './first-step';
import {SecondStep} from './second-step';
// import {ThreeStep} from './three-step';

const Register = ({page, setCurrentPage, isMenuShow, setIsMenuShow, width, country, years}) => {
  const [step, setStep] = useState(1)
  const [finishFirstStep, setFinishFirstStep] = useState(null)
  const [finishSecondStep, setFinishSecondStep] = useState(null)
  const [dataCountry, setDataCountry] = useState(null)
  const [flagsPhoneCode, setFlagsPhoneCode] = useState(null)
  const [modal, setModal] = useState(false);
  useEffect(() => {
    setDataCountry([...country.map(({id, alias, name}) => ({id, alias, name}))])
    setFlagsPhoneCode([...country.map(({id, alias, calling_codes}) => ({id, alias, calling_codes})).filter(i => i.calling_codes && i)].sort(function (a, b) {
      if (a.calling_codes > b.calling_codes) {
        return 1;
      }
      if (a.calling_codes < b.calling_codes) {
        return -1;
      }
      return 0;
    }))
  }, [country])

  useEffect(() => {

  }, [finishFirstStep, finishSecondStep])
  return (
    <Layout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}>
      <div className='register'>
        {step === 1
          ? <>
            <h1 className='title'>{page}</h1>
            <div className='steps'>
              {width > 680 && <div className={cn('step', {'current-step': step === 2})}/>}
            </div>
            <FirstStep dataCountry={dataCountry} flagsPhoneCode={flagsPhoneCode} setModal={setModal} setCurrentPage={setCurrentPage} setStep={setStep} setFinish={setFinishFirstStep}/>
          </>
          :  <SecondStep step={step} setStep={setStep} setFinish={setFinishSecondStep}
                          width={width} years={years}
                          id={finishFirstStep.id}
                          initials={finishFirstStep.first_name[0] + finishFirstStep.last_name[0]}
                          name={finishFirstStep.first_name + ' ' + finishFirstStep.last_name}/>
            // : <ThreeStep/>
        }
      </div>
      {modal && <ModalCard reason={modal.reason ? modal.reason : 'auth'} message={!modal.reason && modal} setModal={setModal}/>}
    </Layout>
  )
};

export default connect(({page, windowSize, country, years}) => ({...page, width: windowSize.width, country, years}), {setCurrentPage})(Register);
