import axios from 'axios';
const baseUrl = 'https://case-university.com/api/v1/';

export const api = {
  login: async (data) => {
    const formData = new FormData();
    formData.append('email', data.email);
    formData.append('password', data.password);
    return axios.post(baseUrl + 'user/auth', formData)
  },

  logout: async () => axios.get(baseUrl + 'user/logout'),
  createUser: async (data) => axios.post(baseUrl + 'user/create', data),
  profileUpdate: async (data) => axios.post(baseUrl + 'user/profile/update', data),
  avatarUpdate: async (data) => {
    const formData = new FormData();
    formData.append('id', data.id);
    formData.append('photo', data.photo);
    return axios.post(baseUrl + 'user/profile/avatar_update', formData)
  },
  partners: async (data) => axios.get(baseUrl + `partners/get?${data}`),

  recovery: async (data) => {
    const formData = new FormData();
    formData.append('email', data.email);
    return axios.post(baseUrl + 'user/password/recovery', formData)
  },

  updatePassword: async (data) => {
    const formData = new FormData();
    formData.append('new_password1', data.new_password1);
    formData.append('new_password2', data.new_password2);
    formData.append('confirmation_token', data.confirmation_token);
    formData.append('uid', data.uid);
    return axios.post(baseUrl + 'user/password/change', formData)
  },

  userProfile: async (id) => axios.get(baseUrl + `partners/get/${id}/`),
  userContacts: async (id) => axios.get(baseUrl + `partners/contacts/get?user=${id}`),

  country: async () => axios.get(baseUrl + 'user/country'),
  language: async () => axios.get(baseUrl + 'user/language'),
  communication: async () => axios.get(baseUrl + 'user/communication'),
  tags: async () => axios.get(baseUrl + 'user/tags'),

  settingsUpdate: async (data) => {
    const formData = new FormData();
    data.options.forEach(({id, checked}) => formData.append(id, checked));
    formData.append('id', data.id);
    return axios.post(baseUrl + 'user/profile/settings', formData);
  },

  contactDelete: async (data) => axios.get(baseUrl + `partners/contact/delete?from_user=${data.from_user}&to_user=${data.to_user}`),
  contactApprove: async (data) => axios.get(baseUrl + `partners/contact/approve?from_user=${data.from_user}&to_user=${data.to_user}`),
  contactAdd: async (data) => axios.get(baseUrl + `partners/contact/add?from_user=${data.from_user}&to_user=${data.to_user}`),

  getMaterialCards: async (data) => axios.get(baseUrl + `/materials/get?${data}`),
  getMaterialFaq: async () => axios.get(baseUrl + '/materials/faq'),
  getMaterialVideo: async () => axios.get(baseUrl + '/materials/video'),
  getMaterialIntroduction: async () => axios.get(baseUrl + '/materials/introduction'),
}
