import React, {useState, useEffect} from 'react';
import {Checkbox, Button} from 'antd';
import {api} from "../../../utils/api";
import ModalCard from '../../../utils/modal-card';
import './index.scss';

const options2 = [
  {id: 'notification_about_adding', checked: false, text: 'New buddy added'},
  {id: 'feedback_solution', checked: false, text: 'New feedback received'},
  {id: 'newsletter', checked: false, text: 'Newsletter'}
]

const Settings = ({id, onPage}) => {
  const [isModal, setIsModal] = useState(null)
  const [options, setOptions] = useState(options2)

  const onSettings = ({id, checked, text}) => {
    setOptions([...options.map(op => op.id === id ? {id, text, checked} : op)])
  }

  const setSettingsUpdate = async () => {
    // let s = [...options.map(({id, checked}) => ({[id]: checked}))]
    const res = await api.settingsUpdate({id: +id, options})
    res.data ? setIsModal('hanges saved') : setIsModal('settings error')
    res.data && localStorage.setItem('settings', JSON.stringify([...options.map(i => ({...i, checked: res.data[i.id]}))]))
  }

  useEffect(() => {
    onPage('settings')
    localStorage.getItem('settings') && setOptions(JSON.parse(localStorage.getItem('settings')))
  }, [])

  return (
    <div className='settings'>
      <div className='settings__label'>
        Notifications by e-mail:
      </div>
      {options && options.map(op =>
        <div key={op.id}>
          <Checkbox checked={op.checked} value={op.text} onChange={() => onSettings({id: op.id, checked: !op.checked, text: op.text})}>
            {op.text}
          </Checkbox>
        </div>
      )}
      <div className='settings__btn'>
        <Button className='blue' onClick={setSettingsUpdate}>Save changes</Button>
      </div>
      {isModal && <ModalCard reason={isModal} setModal={setIsModal}/>}
    </div>
  );
};

export default Settings;
