import React, {useEffect, useState} from 'react';
import {Button, Checkbox, Form, Input, Select} from 'antd';
import {connect} from 'react-redux';
import add from '../../../common/images/auth/add.svg';
import {open} from '../../../common/images/partners';
import {setUserProfileData, USER_IS_SECOND_STEP, USER_PROFILE_DATA, setUserIsSecondStep, SET_USER_NAME, setUserName} from '../../../redux/reducers/user';
import {api} from '../../../utils/api';
import ModalCard from '../../../utils/modal-card';
import './index.scss';
import {SelectItem} from '../../../utils/supports';

const Profile = ({onPage, id, country, width, years, profileData, first_name, last_name, setUserName, setUserProfileData, setUserIsSecondStep, searchAccess, defTags, defCommunication}) => {
  const [communication, setCommunication] = useState([...defCommunication])
  const [isModal, setIsModal] = useState(false)
  const [linkText, setLinkText] = useState('')
  const [inputText, setInputText] = useState('')
  const [links, setLinks] = useState([])
  const [language, setLanguage] = useState([])
  const [tags, setTags] = useState([...defTags])
  const [isLoading, setIsLoading] = useState(true)
  const [communicationError, setCommunicationError] = useState(false)
  const [tagsError, setTagsError] = useState(false)

  const onTags = ({id, checked, name}) => {
    setTags([...tags.map(op => op.id === id ? {id, name, checked} : op)])
  }

  const onCommunication = ({id, checked, name}) => {
    setCommunication([...communication.map(op => op.id === id ? {id, name, checked} : op)])
  }

  useEffect(() => {
    profileData.search_target && setTags([...tags.map(i => profileData.search_target.find(j => j.id === i.id) ? {...i, checked: true} : i)])
    profileData.communication_method && setCommunication([...communication.map(i => profileData.communication_method.find(j => j.id === i.id) ? {...i, checked: true} : i)])
    setLinks(profileData.contacts?[...profileData.contacts]:[])
  }, [profileData])

  const dropLink = (link) => setLinks(links.filter(l => l !== link))

  const onInputText = e => {
    setLinkText(e.target.value)
    setInputText(e.target.value)
  }

  const onLinks = () => {
    linkText.length && setLinks([...new Set([...links, linkText])])
    setLinkText('')
    setInputText('')
  }

  const [form] = Form.useForm();

  useEffect(async () => {
    onPage('profile')
    const resLang = await api.language()
    setLanguage(resLang.data)
    setIsLoading(false)
    setTagsError(false)
    setCommunicationError(false)
  }, [])

  useEffect(async () => {
    for (let i = 0; i < communication.length; i++) {
      if (communication[i].checked) {
        setCommunicationError(false)
        break
      }
      communication.length && setCommunicationError(true)
    }
    for (let i = 0; i < tags.length; i++) {
      if (tags[i].checked) {
        setTagsError(false)
        break
      }
      tags.length && setTagsError(true)
    }
  }, [communication, tags])

  const onFinish = (values) => {
    for (let i = 0; i < communication.length; i++) {
      if (communication[i].checked) {
        setCommunicationError(false)
        middlewareFinish(values)
        break
      }
      communication.length && setCommunicationError(true)
    }
    for (let i = 0; i < tags.length; i++) {
      if (tags[i].checked) {
        setTagsError(false)
        middlewareFinish(values)
        break
      }
      tags.length && setTagsError(true)
    }
  };

  const middlewareFinish = (values) => {
    !communicationError && !tagsError && onRequestFinish(values)
  }

  const onRequestFinish = async (values) => {
    let tagsFinish = [...tags.filter(op => op.checked).map(({id}) => id)]
    let communicationFinish = [...communication.filter(op => op.checked).map(({id}) => id)]
    const res = await api.profileUpdate({...values, communication: communicationFinish, tags: tagsFinish, searchAccess, id, links})
    localStorage.removeItem('profileData')
    setUserProfileData(USER_PROFILE_DATA, res.data)
    localStorage.setItem('is_second_step', res.data.is_second_step)
    localStorage.setItem('name', JSON.stringify({first_name: res.data.user.first_name, last_name: res.data.user.last_name}))
    setUserName(SET_USER_NAME, res.data.user.first_name, res.data.user.last_name)
    setUserIsSecondStep(USER_IS_SECOND_STEP, res.data.is_second_step)
    setIsModal(true)
  }

  if (isLoading) return <div>Loading</div>

  return (
    <div className='profile'>
      <Form
        form={form}
        name='profile'
        onFinish={onFinish}
        initialValues={{
          ...{
            ...profileData,
            selectLanguage: profileData.language ? (profileData.language.length ? profileData.language[0].name : '') : '',
            country: JSON.parse(localStorage.getItem('profileData')).country.name
              ? JSON.parse(localStorage.getItem('profileData')).country.name
              : JSON.parse(localStorage.getItem('profileData')).country
          },
          first_name, last_name}}
        scrollToFirstError>

        <Form.Item
          name='first_name'
          label='First name'
          rules={[{required: true, message: 'Please input your First name!', whitespace: true},
            {
              validator: (_, value) => {
                if (/^[A-Za-z0-9\s]+$/g.test(value)) {
                  return Promise.resolve()
                }
                return Promise.reject('Use only Latin letters!')
              }
            }]}>
          <Input/>
        </Form.Item>
        <Form.Item
          name='last_name'
          label='Last name'
          rules={[{required: true, message: 'Please input your Last name!', whitespace: true},
            {
              validator: (_, value) => {
                if (/^[A-Za-z0-9\s]+$/g.test(value)) {
                  return Promise.resolve()
                }
                return Promise.reject('Use only Latin letters!')
              }
            }]}>
          <Input/>
        </Form.Item>

        <Form.Item name='university' label='University' rules={[{required: true, message: 'Please input your University!'},{
          validator: (_, value) => {
            if (/^[A-Za-z0-9\s]+$/g.test(value)) {
              return Promise.resolve()
            }
            return Promise.reject('Use only Latin letters!')
          }
        }]}>
          <Input/>
        </Form.Item>
        <Form.Item name='faculty' label='Faculty' rules={[{required: true, message: 'Please input your Faculty!'},{
          validator: (_, value) => {
            if (/^[A-Za-z0-9\s]+$/g.test(value)) {
              return Promise.resolve()
            }
            return Promise.reject('Use only Latin letters!')
          }
        }]}>
          <Input/>
        </Form.Item>
        <Form.Item name='year_of_graduation' label='Graduation year' rules={[{required: true, message: 'Please input your year!'}]}>
          <Select className='ant-select-customize-input' onChange={null} placeholder='Please select a year'>
            {years && years.map(year => <Select.Option key={year} value={year}>{year}</Select.Option>)}
          </Select>
        </Form.Item>

        <Form.Item label='Social media' rules={[{
          validator: (_, value) => {
            if (/^[A-Za-z0-9\s]+$/g.test(value)) {
              return Promise.resolve()
            }
            return Promise.reject('Use only Latin letters!')
          }
        }]}>
          <div className='flex-align-center' style={{position: 'relative'}}>
            <Input placeholder='Enter links' value={inputText} onChange={onInputText} style={{padding: '19px 30px 19px 11px'}}/>
            <img className='form__link-add' src={width < 681 ? open : add} alt='add new link' onClick={onLinks}
                 style={{position: 'absolute', right: width < 681 ? '12px' : '-80px', cursor: 'pointer'}}/>
          </div>
        </Form.Item>

        <div className='links'>
          {!!links.length && links.map((link, i) => <SelectItem key={link + i} option={link} dropSelect={dropLink}/>)}
        </div>

        <Form.Item className='border-none' rules={[{required: true, message: 'Please input your Tags!'}]} label='Tags'>
          {tags&&tags.map(op =>
            <div key={op.name}>
              <Checkbox checked={op.checked} value={op.name} onChange={() => onTags({id: op.id, checked: !op.checked, name: op.name})}>
                {op.name}
              </Checkbox>
            </div>
          )}
          <div className='checkbox-err'>{tags.length && tagsError && 'Select at least one checkbox'}</div>
        </Form.Item>

        <Form.Item name='country' label='Country' rules={[{required: true, message: 'Please input your country!'}]}>
          <Select className='ant-select-customize-input' onChange={null} placeholder='Please select a country'>
            {country && country.map(({alias, name}) =>
              <Select.Option key={name} value={name}>
                <img style={{borderRadius: '50%'}} src={'https://www.countryflags.io/' + alias + '/flat/24.png'} alt='flat country'/>&nbsp;{name}
              </Select.Option>)}
          </Select>
        </Form.Item>
        <Form.Item
          rules={[{required: true, message: 'Please input your language!'}]}
          name='selectLanguage'
          label='Language'>
          <Select className='ant-select-customize-input' placeholder='Please select a language'>
            {language && language.map(l => <Select.Option key={l.name} value={l.name}>{l.name}</Select.Option>)}
          </Select>
        </Form.Item>
        <Form.Item name='bio' label='Any additional information' rules={[{
          validator: (_, value) => {
            if (/^[A-Za-z0-9.,?!)(\[\]}@#$%^&*\/\\";%{_\'\"\-+=\s]+$/g.test(value)) {
              return Promise.resolve()
            }
            return Promise.reject('Use only Latin letters!')
          }
        }]}>
          <Input.TextArea placeholder='Free format'/>
        </Form.Item>
        <Form.Item className='border-none'
                   label='I prefer to communicate by'>
          {communication && communication.map(op =>
            <div key={op.name}>
              <Checkbox checked={op.checked} value={op.name} onChange={() => onCommunication({id: op.id, checked: !op.checked, name: op.name})}>
                {op.name}
              </Checkbox>
            </div>
          )}
          <div className='checkbox-err'>{communication.length && communicationError && 'Select at least one checkbox'}</div>

        </Form.Item>

        <Form.Item className='border-none'>
          <Button className='blue mt30' htmlType='submit' shape='round'>
            Save changes
          </Button>
        </Form.Item>
      </Form>
      {isModal && <ModalCard reason='hanges saved' setModal={setIsModal}/>}
    </div>
  );
};

export default connect(({user: {id, first_name, last_name, profileData}, country, years, windowSize}) =>
  ({id, first_name, last_name, country, profileData, years, width: windowSize.width}), {setUserProfileData, setUserName, setUserIsSecondStep})(Profile);
