import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import {Switch, Route} from 'react-router-dom';
import Auth from './components/Auth';
import CaseChallenge from './components/CaseChallenge';
import Cases from './components/Cases';
import Main from './components/Main';
import Materials from './components/Materials';
import Buddies from './components/Buddies';
import PrivateAccount from './components/PrivateAccount';
import User from './components/User';
import {SET_COUNTRY, setCountry} from './redux/reducers/country';
import {
  SET_USER_ID,
  SET_USER_NAME,
  setUserAvatar,
  setUserId,
  setUserIsAuth,
  setMeAdded,
  setUserName,
  setUserProfileData,
  USER_IS_AUTH,
  USER_ME_ADDED,
  USER_PROFILE_DATA,
} from './redux/reducers/user';
import {api} from './utils/api';
import MainLayout from './utils/layout/main';
import ModalCard from './utils/modal-card';
import NotFound from './utils/service';
import {resizeWidth, RESIZE_WINDOW} from './redux/reducers/window';

const App = ({isAuth, setUserId, id, setMeAdded, setUserName, resizeWidth, setUserProfileData, setCountry, setUserIsAuth}) => {
  const [isMenuShow, setIsMenuShow] = useState(false)
  const [isModal, setIsModal] = useState(false)
  const [showCookie, setShowCookie] = useState(false)
  const [isLoading, setIsLoading] = useState(true)

  const onClick = e => {
    e.target.classList.length ?
      ((e.target.classList[0].includes('user') || e.target.dataset.user) && !isMenuShow ? setIsMenuShow(true) : setIsMenuShow(false))
      : setIsMenuShow(false)
  }

  const onUser = async ({id, first_name, last_name, profileData}) => {
    id && setUserId(SET_USER_ID, id)
    localStorage.getItem('token') && setUserIsAuth(USER_IS_AUTH, localStorage.getItem('token'))
    first_name && last_name && setUserName(SET_USER_NAME, first_name, last_name)
    profileData && setUserProfileData(USER_PROFILE_DATA, profileData)
    // !localStorage.getItem('photo') && setUserAvatar(SET_USER_AVATAR, profileData.photo)
    // setUserIsSecondStep(USER_IS_SECOND_STEP, is_second_step)
  }

  const onMeAdded = async () => {
    const resPartners = await api.userContacts(+id)
    let i = 0;
    resPartners && resPartners.data.forEach(p => !p.approved && !(+p.from_user.id === +id) && ++i)
    setMeAdded(USER_ME_ADDED, i)
  }

  const setHideCookie = () => {
    setShowCookie(false)
    !localStorage.getItem('showCookie') && localStorage.setItem('showCookie', 'true')
  }

  // useEffect(() => {
  //   debugger
  //   !showCookie && setShowCookie(!localStorage.getItem('showCookie'))
  // }, [showCookie])

  const onResize = e => resizeWidth(RESIZE_WINDOW, e.target.innerWidth)
  useEffect(async () => {
    !showCookie && setShowCookie(!localStorage.getItem('showCookie'))
    // !!localStorage.getItem('token') && localStorage.getItem('token') !== null && setUserIsAuth(USER_IS_AUTH, true)
    !!localStorage.getItem('uid') && await onUser({id: localStorage.getItem('uid')})
    !!localStorage.getItem('name') && await onUser(JSON.parse(localStorage.getItem('name')))
    !!localStorage.getItem('profileData') && await onUser({profileData: JSON.parse(localStorage.getItem('profileData'))})
    onMeAdded()
    resizeWidth(RESIZE_WINDOW, document.documentElement.clientWidth)
    window.addEventListener('resize', onResize)
    setIsLoading(false)
    const country = await api.country()
    setCountry(SET_COUNTRY, country.data)
    return () => {
      window.removeEventListener('resize', onResize)
    }
  }, [])

  if (isLoading) return <div>Loading</div>

  return (
    <div className='app' onClick={onClick}>
      <Switch>

        <Route path='/' exact render={() =>
          <MainLayout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow} onMeAdded={onMeAdded}>
            <Main/>
          </MainLayout>}/>

        <Route path='/cases' exact render={() =>
          <MainLayout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}  onMeAdded={onMeAdded}>
            <div className='container'><Cases onMeAdded={onMeAdded}/></div>
          </MainLayout>}/>

        {/*<Route path='/case-challenge' exact render={() =>*/}
        {/*  <MainLayout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}  onMeAdded={onMeAdded}>*/}
        {/*    <div className='container'><CaseChallenge/></div>*/}
        {/*  </MainLayout>}/>*/}

        <Route path='/materials' render={() =>
          <MainLayout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}  onMeAdded={onMeAdded}>
            <div className='container'><Materials onMeAdded={onMeAdded}/></div>
          </MainLayout>}/>

        <Route path='/buddies' render={() =>
          <MainLayout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}  onMeAdded={onMeAdded}>
            <div className='container'><Buddies onMeAdded={onMeAdded}/></div>
          </MainLayout>}/>

        <Route path='/user' render={() =>
          <MainLayout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}  onMeAdded={onMeAdded}>
            <div className='container'><User onMeAdded={onMeAdded}/></div>
          </MainLayout>}/>

        <Route path='/account' render={() =>
          <MainLayout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}  onMeAdded={onMeAdded}>
            <div className='container'><PrivateAccount onMeAdded={onMeAdded}/></div>
          </MainLayout>}/>

        {!isAuth && <Auth isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}/>}

        <Route render={() => <NotFound/>}/>
      </Switch>

      {!isAuth && isModal && <ModalCard reason='no register' setModal={setIsModal}/>}
      {showCookie &&
      <div className='cookie'>
        <div className='cookie__text'>
          We use cookies in order to improve the quality and usability of the case-university.com website. More information about the use of cookies and the regulations on
          processing personal data can be found here <a href='https://case-university.com/media/privacy_policy_en.pdf' target='_blank'>Privacy Policy</a>.&nbsp; By continuing to
          use the site, you hereby
          confirm that you have been
          informed of the use of cookies by the case-university.com website and agree with our rules for processing personal data. You may disable cookies in your browser settings.
        </div>
        <button className='cookie__btn' onClick={setHideCookie}>Accept</button>
      </div>}
    </div>
  );
}

const mstp = ({user}) => ({isAuth: user.isAuth, token: user.token, id: user.id})

export default connect(mstp, {resizeWidth, setCountry, setUserId, setUserAvatar, setUserProfileData, setMeAdded, setUserName, setUserIsAuth})(App)

const Container = ({childen}) => <div className='container'>{childen}</div>
