import React, {useState} from 'react';
import cn from 'classnames';
import {connect} from 'react-redux';
import {SET_USER_AVATAR, setUserAvatar} from '../../redux/reducers/user';
import {api} from '../api';
import ModalCard from '../modal-card';
import './index.scss';

const Avatar = ({src, setSrc, icon, initials, data, meAdded, setUserAvatar, uid}) => {
  const [modal, setModal] = useState(false)

  const readURL = async (e) => {
    if (e.target.files && e.target.files[0]) {
      // localStorage.setItem('photo', e.target.result)
      try {
        const res = await api.avatarUpdate({photo: e.target.files[0], id: +localStorage.getItem('uid') || uid})
        res.data.id && setSrc('http://case-university.com/' + res.data.photo.replace(/^[a-zA-Z]{3,5}\:\/{2}[a-zA-Z0-9_.:-]+\//, ''))
        res.data.id && localStorage.setItem('photo', 'http://case-university.com/' + res.data.photo.replace(/^[a-zA-Z]{3,5}\:\/{2}[a-zA-Z0-9_.:-]+\//, ''))
        res.data.id && setUserAvatar(SET_USER_AVATAR, 'http://case-university.com/' + res.data.photo.replace(/^[a-zA-Z]{3,5}\:\/{2}[a-zA-Z0-9_.:-]+\//, ''))
        Array.isArray(res.data.photo) && setModal(res.data.photo[0])

      } catch (e) {
        setModal(e.message)
      }
    }
  }

  return (
    <div className='avatar' style={!icon ? {padding: '0'} : {}}>
      {icon
        ? <>
          <label htmlFor='avatar-upload'>
            <div className='upload-button'>
              {(src && src !== 'default.png' && src !== 'http://case-university.com/media/default.png')
                ? <img className='profile-pic' style={{minWidth: '110px'}} src={'http://case-university.com/' + src.replace(/^[a-zA-Z]{3,5}\:\/{2}[a-zA-Z0-9_.:-]+\//, '')}
                       alt='avatar'/>
                : <div className='initials'>
                  <div className='initials__inner'>{initials.toUpperCase()}</div>
                </div>
              }
            </div>
            <div className={cn({'add-photo': !!icon})} style={icon && {backgroundImage: `url(${icon})`}}/>
          </label>
          <input className='file-upload' id='avatar-upload' type='file' accept='image/*' onChange={readURL}/>
        </>
        : <>
          {(src && src !== 'default.png' && src !== 'http://case-university.com/media/default.png')
            ?
            <div className='avatar__inner'>
              <img className='profile-pic'
                   src={'http://case-university.com/' + src.replace(/^[a-zA-Z]{3,5}\:\/{2}[a-zA-Z0-9_.:-]+\//, '')} alt='avatar' data-user={data}/>
              {!!meAdded && <span className='avatar__me-added'>{meAdded}</span>}
            </div>
            : <div className='initials' data-user={data} style={{position: 'relative'}}>
              <div className='initials__inner' data-user={data}>{initials.toUpperCase()}</div>
              {!!meAdded && <span className='avatar__me-added avatar__me-added--initials'>{meAdded}</span>}
            </div>
          }
        </>
      }
      {modal && <ModalCard reason='auth' message={modal} setModal={setModal}/>}
    </div>
  );
};

export default connect(null, {setUserAvatar})(Avatar);
