import React from 'react';
import {NavLink} from 'react-router-dom';
import {Button} from 'antd';
import Layout from '../../../utils/layout/service';

const MailConfirmed = () => {
  return (
    <Layout>
      <div className='mail-not-confirmed'>
        <h1 className='title'>Your mail has been verified!</h1>
        <div className='mail-not-confirmed__text'>
          You can now log in!
        </div>
        <Button className='blue'><NavLink to='/login'>Go to login</NavLink></Button>
      </div>
    </Layout>
  );
};

export default MailConfirmed;
