import React, {useEffect, useState, useRef} from 'react';
import {Redirect} from 'react-router-dom';
import {Form, Input, Button} from 'antd';
import {connect} from 'react-redux';
import {NavLink} from 'react-router-dom';
import {api} from '../../../utils/api';
import {NoVisible, Visible} from '../../../utils/icon/eye';
import Layout from '../../../utils/layout/service';
import {setCurrentPage, CURRENT_PAGE} from '../../../redux/reducers/page';
import {
  SET_USER_ID,
  USER_IS_SECOND_STEP,
  USER_PROFILE_DATA,
  setUserId,
  setUserIsAuth,
  setUserIsSecondStep,
  setUserToken,
  SET_USER_TOKEN,
  USER_IS_AUTH,
  SET_USER_NAME,
  setUserName, setUserAvatar,
  setUserProfileData, SET_USER_AVATAR
} from '../../../redux/reducers/user';
import {Key} from '../../../utils/icon/key';
import ModalCard from '../../../utils/modal-card';

const Login = ({setCurrentPage, setUserIsAuth, isMenuShow, setUserName, setIsMenuShow, setUserProfileData, setUserAvatar, setUserIsSecondStep, setUserToken, setUserId}) => {
  const [isVisible, setIsVisible] = useState(false);
  const [modal, setModal] = useState(false);
  const [redirectMainPage, setRMP] = useState(false);

  const onUser = (data) => {
    setRMP(true)
    setUserId(SET_USER_ID, data.id)
    setUserToken(SET_USER_TOKEN, data.authenticate_toket)
    setUserIsAuth(USER_IS_AUTH, true)
    setUserName(SET_USER_NAME, data.first_name, data.last_name)
    setUserIsSecondStep(USER_IS_SECOND_STEP, data.is_second_step)
    setUserProfileData(USER_PROFILE_DATA, data.profile_data)
    setUserAvatar(SET_USER_AVATAR, 'http://case-university.com/media/'+data.profile_data.photo)
  }

  const onFinish = async (values) => {
    setUserIsAuth(USER_IS_AUTH, true)
    const res = await api.login(values);
    res.data.result === 'error'
      ? setModal({message: res.data.message})
      : res.data.is_confirmed_email
      ? onUser(res.data)
      : setModal({reason: 'no confirmation email'})
    res.data.result !== 'error' && localStorage.setItem('isAuth', 'true')
    res.data.result !== 'error' && localStorage.setItem('is_second_step', res.data.is_second_step)
  };

  const passwordRef = useRef(null)

  useEffect(() => {
    isVisible
      ? passwordRef.current.input.type = 'text'
      : passwordRef.current.input.type = 'password'
  }, [isVisible])

  useEffect(() => setCurrentPage(CURRENT_PAGE, 'Log in'), [])

  if (redirectMainPage) return <Redirect to='/'/>

  return (
    <Layout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}>
      <div className='login'>
        <h1 className='title'>Log in</h1>
        <Form
          name='login'
          className='login-form'
          initialValues={{remember: true}}
          onFinish={onFinish}>

          <Form.Item
            name='email'
            label='Email'
            rules={[
              {
                type: 'email',
                message: 'The input is not valid E-mail!',
              },
              {
                required: true,
                message: 'Please input your E-mail!',
              },
            ]}>
            <Input placeholder='Email'/>
          </Form.Item>

          <Form.Item
            className='login-password'
            name='password'
            label='Password'
            rules={[{required: true, message: 'Please input your password!'}]}>
            <Input.Password placeholder='Password'
                            ref={passwordRef}
                            iconRender={_ => (isVisible
                              ? <Visible isVisible={isVisible} setIsVisible={setIsVisible}/>
                              : <NoVisible isVisible={isVisible} setIsVisible={setIsVisible}/>)}
            />
          </Form.Item>

          <div className='login-form-forgot'>
            <NavLink className='login-link' to='/forgot'>
              <Key className='site-form-item-icon'/>
              Forgotten your password?
            </NavLink>
          </div>

          <Form.Item className='border-none'>
            <Button className='blue mt30' htmlType='submit' shape='round' size='large'>
              Log in
            </Button>
          </Form.Item>
          <div className='auth-link'>
            <NavLink to='/registration' className='auth-arrow-icon'>No account? Sign up</NavLink>
          </div>

        </Form>
      </div>
      {modal && <ModalCard reason={modal.reason ? modal.reason : 'auth'} message={modal.message} setModal={setModal}/>}
    </Layout>
  );
};

export default connect(null, {setCurrentPage, setUserIsAuth, setUserId, setUserAvatar, setUserIsSecondStep, setUserProfileData, setUserName, setUserToken})(Login);
